.PokemonList {
  width: 95%;
  margin: 0 auto;
  display: grid;
  padding: 1rem;
  gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.Pokemon {
  padding: 1rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: 250ms;
  cursor: pointer;
  box-shadow: 3px 3px 8px #383838, inset 6px 6px 12px rgba(0, 0, 0, 0.2),
    inset -10px -10px 20px rgba(0, 0, 0, 0.3);
}

.Pokemon:hover {
  transform: scale(1.01) translateY(-0.25rem);
}

.Type {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 0.25rem;
}
